html, body {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

*, :before, :after {
  box-sizing: border-box;
}

a {
  color: #000;
  text-decoration: none;
}

p {
  margin: 0;
  padding: 0;
}

.spacer {
  padding: 20px 0;
}

.loader {
  z-index: 99999;
  transition: all 200 ease-in-out;
  background-color: #d9d9d9;
  justify-content: center;
  align-items: center;
  width: 100vw;
  height: 100vh;
  display: none;
  position: absolute;
  top: 0;
  left: 0;
}

.loading__text {
  font-family: Bonheur Royale, cursive;
  font-size: 68px;
  font-weight: 400;
  transition: opacity .3s ease-in-out;
