  font-size: 34px;
}

.main__btn a {
  opacity: .2;
}

.hero {
  justify-content: center;
  align-items: center;
  height: 420px;
  padding: 0 39px;
  display: flex;
}

.hero h1 {
  font-family: Cinzel Decorative, serif;
  font-size: 40px;
}

.page__navigation {
  padding: 0 39px;
}

.page__navigation > ul {
  grid-column-gap: 16px;
  grid-template-rows: 1px;
  grid-template-columns: repeat(4, 1fr);
  width: 100%;
  margin: 0;
  padding: 0;
  font-family: Bonheur Royale, cursive;
  list-style: none;
  display: grid;
