.item ul li a {
  color: #fff;
  padding: 10px 16px 10px 48px;
  font-size: 24px;
  display: block;
}

.item ul li:hover {
  background-color: #ffffff4d;
}

.item p:hover + .hidden, .hidden:hover {
  background-color: #00000080;
  display: block;
}

.layout {
  flex: 1;
  padding: 0 39px;
  display: block;
}

.not-found {
  justify-content: center;
  align-items: center;
  display: flex;
}

.not-found div {
  text-align: center;
  width: 40%;
  max-width: 1000px;
}

.not-found img {
