.qr-container {
  padding: 20px 0;
}

.ilustracion-wrapper {
  flex-direction: column;
  display: flex;
}

.ilustracion {
  grid-gap: 10px;
  grid-template-rows: repeat(2, 1fr);
  grid-template-columns: repeat(4, 1fr);
  width: 100%;
  display: grid;
}

.illustration-item {
  display: block;
}

.illustration-item img {
  object-fit: cover;
  width: 100%;
  height: 100%;
  display: block;
}

.illustration-item-1 {
  grid-area: 1 / 1 / 3 / 2;
}

.illustration-item-2 {
  grid-area: 1 / 2 / 3 / 3;
}

.illustration-item-3 {
  grid-area: 1 / 3 / 3 / 4;
}

.illustration-item-4 {
  grid-area: 1 / 4 / 2 / 5;
}

.illustration-item-5 {
